@use "../utils";
/*Global dropdown menu styles*/
.dropdown {
	position: relative;

	&.show {
		.dm-toggle {
			&:before {
				opacity: 1;
			}
		}
	}
}
.dm-menu {
	background-clip: padding-box;
	background: var(--color-white);
	border: none;
	display: flex;
	list-style: none;
	margin: 0;
	opacity: 0;
	text-align: left;
	visibility: hidden;
	z-index: 100;

	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		overflow: hidden;
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		pointer-events: none;
		position: absolute;
		top: 100%;
		transform: translate(0, 10px);
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) and (max-width: #{utils.getBreakpoint('xl', true)}) {
		left: 0;
	}
	@media (min-width: #{utils.getBreakpoint('xl')}) {
		left: 50%;
		transform: translate(-50%, 10px);
	}

	&.show {
		display: flex;
		pointer-events: all;
	}
}
.dm-small {
	@include utils.dropdownBase;
max-height: 300px;
overflow-y: scroll;
	&.show {
		animation: animateDropdownUp cubic-bezier(0.77, 0, 0.175, 1) 1;
		animation-fill-mode: both;
		animation-duration: var(--transitions);
	}
	a {
		@include utils.dropdownLink;
	}
	label {
		color: var(--color-black);
		display: flex;
		align-items: center;
		gap: 1em;
		&:hover {
			[type="checkbox"] {
				border-color: var(--color-blue-1);
			}
		}
	}
	[type="checkbox"] {
		margin: 0;
		border: solid 1px var(--color-blue-4);
		transition: border var(--transitions);
		&:checked {
			background: url("/assets/images/checkbox-check.svg") no-repeat
				center center;
		}
	}
}
.dm-toggle {
	align-items: center;
	background: transparent;
	border: none;
	color: var(--color-blue-1);
	display: flex;
	gap: 5px;
	font-size: 1em;

	&:after {
		content: "";
		background: url("/assets/images/caret-blue.svg") no-repeat center center;
		display: block;
		height: 8px;
		width: 10px;
		z-index: 10;
	}
}
