@use "../utils";
.badge {
	font-size: var(--small-text);
	background: var(--color-blue-2);
	align-items: center;
	border-radius: 0.35em;
	color: var(--color-white);
	display: inline-flex;
	line-height: 1;
	padding: 0.5em 0.5em;
	text-transform: uppercase;
	vertical-align: baseline;
	white-space: nowrap;
}
