@use "../utils";
.site-header {
	padding-block: 25px;
	z-index: 10;
	display: flex;
	align-items: center;
	justify-content: space-between;
	a{
		text-decoration: none;
	}
}

/*Hamburger menu*/
.navbar-toggle {
	@include utils.resetButton;
	align-items: center;
	display: flex;
	opacity: 1;
	position: relative;
	transition: var(--transitions);
	visibility: visible;
	z-index: 100;
	gap: 10px;
	color: var(--color-blue-2);
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		display: none;
	}
	&:not(.collapsed){
		.open-icon{
			display: none;
		}
	}
	&.collapsed {
		.close-icon{
			display: none;
		}
	}
}
.toggle-text {
	font-size: #{utils.rem(16)};
}


/*Site branding*/
.site-logo {
	max-width: 70px;
	@media (min-width: #{utils.getBreakpoint('lg')}){
		max-width: 150px;
	}
	img {
		width: 100%;
	}
}

#primaryNavigation {
	display: flex;
	flex-direction: column;
	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		background: var(--color-white);
		left: 0;
		opacity: 0;
		overflow-y: scroll;
		pointer-events: none;
		position: fixed;
		right: 0;
		top: var(--site-header-height);
		transition: var(--transitions);
		visibility: hidden;
		z-index: 100;
		height: calc(100svh - var(--site-header-height));


		&.is-expanded {
			opacity: 1;
			pointer-events: all;
			visibility: visible;
		}
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		align-items: flex-end;
		gap: 20px;
	}
	.site-contact-details {
		display: grid;
		gap: 30px;
		padding: 30px 20px;
		text-align: center;

		@media (min-width: #{utils.getBreakpoint('lg')}) {
			display: none;
		}
	}
}
.nav {
	@include utils.resetList;
	align-items: center;
	display: flex;

	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		flex-direction: column;
	}
}
/*Primary menu*/
.nav-primary {
	position: relative;

	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		margin-inline: 25px;
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		order: 2;
		gap: #{utils.makeClamp(20, 40)};
	}

	a:hover {
		text-decoration: none;
	}

	> li {
		@media (max-width: #{utils.getBreakpoint('lg', true)}) {
			display: flex;
			justify-content: center;
			text-align: center;
			width: 100%;

			&:not(:first-child) {
				border-top: solid 1px var(--color-blue-4);
			}
			&:first-child {
				padding-bottom: 30px;
			}
		}

		@media (max-width: #{utils.getBreakpoint('sm', true)}) {
			.btn {
				width: 100%;
			}
		}

		&.menu-item-has-children {
			position: relative;
			@media (hover: hover) {
				&:hover {
					> a:before {
						opacity: 1;
					}
					> .sub-menu {
						display: flex;
						pointer-events: all;

						animation: animateDropdownUp
							cubic-bezier(0.77, 0, 0.175, 1) 1;
						animation-fill-mode: both;
						animation-duration: 0.5s;
					}
				}
				// keyboard support
				.sub-menu.show{
					display: flex;
					pointer-events: all;

					animation: animateDropdownUp
						cubic-bezier(0.77, 0, 0.175, 1) 1;
					animation-fill-mode: both;
					animation-duration: 0.5s;
				}
			}
			> a {
				align-items: center;
				background: transparent;
				color: var(--color-blue-1);
				display: flex;
				gap: 5px;

				&:after {
					content: "";
					background: url("/assets/images/caret-blue.svg") no-repeat
						center center;
					background-size: 100% auto;
					display: block;
					height: 8px;
					width: 8px;
					z-index: 10;
				}
			}

			&.full-preview {
				.sub-menu {
					.body {
						display: none;
					}
					.preview {
						flex: 1;
						color: var(--color-white);
						text-align: center;
						.accent {
							&::before {
								margin-inline: auto;
							}
						}
						p {
							margin-inline: auto;
							max-width: 50%;
						}
					}
				}
				.preview-heading{
					&::before{
						margin: auto;
					}
				}
				.link-back:before {
					border-bottom-color: var(--color-white);
					border-right-color: var(--color-white);
				}
			}

			> .sub-menu {
				background-clip: padding-box;
				background: var(--color-white);
				display: flex;
				list-style: none;
				opacity: 0;
				text-align: left;
				visibility: hidden;
				z-index: 100;

				@media (max-width: #{utils.getBreakpoint('lg', true)}) {
					bottom: 0;
					flex-direction: column;
					left: 0;
					overflow: auto;
					pointer-events: none;
					position: fixed;
					right: 0;
					top: 100px;
				}
				@media (min-width: #{utils.getBreakpoint('lg')}) {
					pointer-events: none;
					position: absolute;
					top: 100%;
					box-shadow: 0 18px 80px rgba(0, 0, 0, 0.17),
						0 5px 24px rgba(0, 0, 0, 0.11),
						0 2px 10px rgba(0, 0, 0, 0.085),
						0 0.815px 3.623px rgba(0, 0, 0, 0.06);
					min-width: #{utils.makeClamp(600, 700)};
					overflow: hidden;
					&:after {
						background: var(--color-blue-1)
							url("../images/backgrounds/bg-dm.png") center
							center/cover no-repeat;
						bottom: 0;
						content: "";
						display: block;
						pointer-events: none;
						position: absolute;
						right: 0;
						top: 0;
						transform: translateX(100%);
						transition: 0.3s;
						width: #{utils.makeClamp(100, 150)};
					}
				}
				@media (min-width: #{utils.getBreakpoint('lg')}) and (max-width: #{utils.getBreakpoint('xl', true)}) {
					left: 0;
				}
				@media (min-width: #{utils.getBreakpoint('xl')}) {
					left: 50%;
					transform: translateX(-50%);
				}

				&.show {
					@media (max-width: #{utils.getBreakpoint('lg', true)}) {
						top: var(--site-header-height);
						animation: animateDropdownMobile
							cubic-bezier(0.77, 0, 0.175, 1) 1;
						animation-fill-mode: both;
						animation-duration: 0.5s;
						pointer-events: all;
					}
				}

				&.empty-preview {
					@media (min-width: #{utils.getBreakpoint('lg')}) {
						.body {
							flex: 1 0 80%;
						}
						.preview {
							flex: 1 0 20%;
						}
					}
				}

				.body {
					flex: 1;
					@media (max-width: #{utils.getBreakpoint('lg', true)}) {
						text-align: center;
					}
					@media (min-width: #{utils.getBreakpoint('lg')}) {
						flex: 1 0 50%;
						padding: #{utils.makeClamp(30, 50)};
						transition: var(--transitions);
					}
					.title {
						font-family: var(--font-family-headings);
						font-size: var(--font-size-h4);
						letter-spacing: var(--narrow-letter-spacing);
						color: var(--color-blue-1);
						margin-bottom: 10px;
						@media (max-width: #{utils.getBreakpoint('lg', true)}) {
							font-size: #{utils.rem(32)};
						}
						@media (min-width: #{utils.getBreakpoint('lg')}) {
							pointer-events: none;
							&::after {
								display: none;
							}
						}
						&::after {
							content: "";
							background: url("/assets/images/caret-blue.svg")
								no-repeat;
							background-size: 100% auto;
							transform: translate(0, -1px) rotate(-90deg);
							width: 12px;
							height: 10px;
							transition: var(--transitions);
						}
					}
				}

				.body > .menu {
					@include utils.resetList;

					a {
						color: var(--color-black);
						display: block;

						@media (max-width: #{utils.getBreakpoint('lg', true)}) {
							font-size: #{utils.rem(18)};
							padding: 10px 20px;
						}
						@media (min-width: #{utils.getBreakpoint('lg')}) {
							font-size: #{utils.makeClamp(16, 18)};
						}
					}
					li {
						@media (min-width: #{utils.getBreakpoint('lg')}) {
							&:not(:last-child) {
								margin-bottom: 10px;
							}
						}
					}
				}

				.preview {
					--color-accent: var(--color-white);
					background: var(--color-blue-1)
						url("../images/backgrounds/bg-dm.png") center
						center/cover no-repeat;
					color: var(--color-white);
					padding-block: #{utils.makeClamp(70, 100)};
					padding-inline: #{utils.makeClamp(40, 50)};
					transition: var(--transitions);
					overflow-x: hidden;

					@media (min-width: #{utils.getBreakpoint('lg')}) {
						flex: 1 0 50%;
					}

					p {
						font-size: var(--small-text);
						color: var(--color-white);
						min-width: 25ch;
					}
					.btn {
						border-color: var(--color-white);
						color: var(--color-white);
						@media (hover: hover) {
							&:hover {
								background-color: rgba(255, 255, 255, .1);
							}
						}
					}
				}

				.preview-heading{
					font-family: var(--font-family-headings);
					font-size: var(--font-size-h2);
					color: var(--color-white);
					line-height: 1.1;
					@include utils.accent;
					@media (max-width: #{utils.getBreakpoint('lg', true)}) {
						font-size: #{utils.rem(32)};
					}
				}
			}
		}
	}
	> li > a:not(.btn),
	.nav-link {
		font-family: var(--font-family-semi-bold);
		line-height: 1.1;
		color: var(--color-blue-1);
		position: relative;

		@media (max-width: #{utils.getBreakpoint('lg', true)}) {
			font-size: #{utils.rem(18)};
			justify-content: center;
			padding: #{utils.rem(25)} 0;
			width: 100%;

			&:after {
				display: none;
			}
		}
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			font-size: #{utils.makeClamp(16, 18)};
			padding: 1rem 0;

			&:before {
				background: var(--color-red);
				bottom: 0;
				content: "";
				display: block;
				height: 3px;
				left: 0;
				opacity: 0;
				position: absolute;
				right: 0;
				transition: var(--transitions);
			}
		}
	}
	.dropdown {
		display: none;
		@media (min-width: #{utils.getBreakpoint('lg')}) and (max-width: #{utils.getBreakpoint('xl', true)}) {
			position: static;
		}
	}
	.link-back {
		left: 26px;
		position: absolute;
		top: 12px;

		@media (min-width: #{utils.getBreakpoint('lg')}) {
			display: none;
		}
	}
}

@media (max-width: #{utils.getBreakpoint('lg', true)}) {
	#site-cta-desktop {
		display: none;
	}
}
@media (min-width: #{utils.getBreakpoint('lg')}) {
	#site-cta-mobile {
		display: none;
	}
}
/*Secondary menu*/
#menu-secondary {
	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		border-bottom: solid 1px var(--color-blue-4);
		border-top: solid 1px var(--color-blue-4);
		gap:  20px;
		padding-block: 30px;
		margin-inline: 25px;
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		order: 1;
	}

	li {
		position: relative;
		color: var(--color-blue-2);
		font-size: #{utils.rem(16)};
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			font-size: #{utils.makeClamp(13, 14)};
		}
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			&:not(:last-child) {
				a {
					&:after {
						content: "|";
						display: inline-block;
						margin: 0 10px;
					}
				}
			}
		}
	}
	a {
		color: currentColor;
		display: block;
	}
	.menu-item-gtranslate {
		display: flex;
		gap: 7px;
		align-items: center;
		select {
			appearance: none;
			border: 1px solid transparent; // fixes caret cut off
			color: currentColor;
			width: 8ch;
			background: url("/assets/images/caret-blue.svg") no-repeat center
				right;
			background-size: 7px auto;
			font-size: 1em;
			padding-left: 0;
			padding-right: 8px;
		}
	}
}

#site-search{
	--field-color: var(--color-blue-5);
	display: none;
	input{
		position: relative; // fixes focus outline
	}
	button{
		flex-shrink: 0;
		padding: 1em;
		color: var(--color-blue-1);
	}
	[type="submit"]{
		font-family: var(--font-family-semi-bold);
		color: var(--color-red);
		background: var(--field-color);
	}
}
#site-search-open{
	color: var(--color-blue-1);
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		min-height: 60px;
	}
}
#site-search-close{
	padding-right: 0;
}

body.site-search-open{
	#site-search{
		display: flex;
		@media (max-width: #{utils.getBreakpoint('lg', true)}){
			width: 100%;
		}
	}
	#site-search-open{
		display: none;
	}
	@media (min-width: #{utils.getBreakpoint('lg')}){
		.nav-primary{
			.menu-item{
				display: none;
			}
		}
	}
}