.tribe-filter-bar {
	--field-color: var(--color-blue-5);
	margin-right: 0;
	width: var(--vw-max-width);
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		background: var(--color-white);
		box-shadow: 0 0 5px rgba(0,0,0,.2);
		padding: var(--spacing-2);
		left: calc((100% - var(--vw-max-width)) * .5);
	}
	@media (min-width: #{utils.getBreakpoint('lg')}){
		padding-bottom: var(--spacing-6);
		width: #{utils.makeClamp(180, 330)};
		border: 0;
	}
}
.tribe-filter-bar-c-filter{
	border-top: solid 1px var(--color-blue-4);
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		&:first-child{
			border: 0;
		}
	}
}
.tribe-filter-bar-c-filter__toggle{
	padding-block: var(--spacing-2);
	font-size: var(--font-size-base);
	&:focus{
		outline: none;
		font-family: var(--font-family-semi-bold);
	}
}
.tribe-filter-bar-c-filter__container{
	margin: 0;
	padding: 10px;
}

.tribe-common-form-control-checkbox__input{
	@include utils.choiceInput;
	&:checked{
		+ .tribe-common-form-control-checkbox__label{
			@include utils.checkboxLabelChecked;
		}
	}
}
.tribe-common-form-control-checkbox__label{
	@include utils.choiceLabel;
	@include utils.checkboxLabelBase;
}
.tribe-common-form-control-radio__input{
	@include utils.choiceInput;
	&:checked{
		+ .tribe-common-form-control-radio__label{
			@include utils.radioLabelChecked;
		}
	}
}
.tribe-common-form-control-radio__label{
	@include utils.choiceLabel;
	@include utils.radioLabelBase;
}
.tribe-filter-bar__selected-filters-list{
	margin: 0;
	gap: 8px;
}

.tribe-filter-bar__selected-filters-list-item{
	padding: 0;
}
.tribe-filter-bar-c-pill__pill{
	border-radius: 4px;
	background: var(--color-blue-5);
	font-size: var(--small-text);
}
