@use "../utils";
/*Inline page filters*/
.filters {
	--field-color: var(--color-blue-5);
	--gap: 15px;
	align-items: center;
	color: var(--color-blue-1);
	display: flex;
	gap: var(--gap);
	flex-wrap: wrap;
	font-size: var(--small-text);

	@media (max-width: #{utils.getBreakpoint('md', true)}) {
		font-size: utils.rem(14);
		width: 100%;
	}
	@media (min-width: #{utils.getBreakpoint('md')}) {
		--gap: #{utils.makeClamp(20, 35)};
	}

	ul {
		@include utils.resetList;
	}

	form {
		> ul {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			gap: var(--gap);
		}
	}

	.choice {
		position: relative;
		label {
			@include utils.choiceLabel;
		}
		input {
			@include utils.choiceInput;
		}
		&.checkbox {
			input:checked + label {
				@include utils.checkboxLabelChecked;
			}
			
			label {
				@include utils.checkboxLabelBase;
			
			}
		}
		&.radio {
			input:checked + label {
				@include utils.radioLabelChecked;
			}
			
			label {
				@include utils.radioLabelBase;
				
			}
		}
	}

	.dropdown {
		.dm-menu {
			right: 0;
			left: auto;
			transform: translateY(10px);
			gap: 15px;
			padding: 20px;
		}
	}
	.reset,
	.submit {
		display: none;
		button {
			background: var(--color-blue-5);
			border: solid 1px var(--color-blue-4);
			display: block;
			padding: 0.25em 0.5em;
			&:hover {
				border-color: var(--color-blue-2);
			}
		}
	}
	.reset.active {
		display: list-item;
	}
}
.filters-sub {
	@include utils.resetList;
}

#filters-search {
	display: flex;
	gap: 1em;
	form {
		background: var(--color-blue-5);
		display: flex;
		input {
			@media (min-width: #{utils.getBreakpoint('lg')}){
				min-width: 45ch;
			}
		}
		button {
			padding: 1em;
			font-family: var(--font-family-semi-bold);
			color: var(--color-red);
		}
	}
	> button {
		color: var(--color-blue-1);
	}
}
#filters-search-open {
	@media (min-width: #{utils.getBreakpoint('lg')}){
		min-height: 60px; // fixes shift when search field is visible
	}
}
#filters-search form,
#filters-search-close {
	display: none;
}
body.filters-search-open {
	#filters-search-close {
		display: block;
	}
	#filters-search form {
		display: flex;
	}
	.filters > form,
	#filters-search-open {
		display: none;
	}
}

@media (max-width: #{utils.getBreakpoint('md', true)}) {
	.filters-submit {
		width: 100%;
	}
}
