/*Global dropdown menu styles*/
.dropdown {
  position: relative;
}
.dropdown.show .dm-toggle:before {
  opacity: 1;
}

.dm-menu {
  background-clip: padding-box;
  background: var(--color-white);
  border: none;
  display: flex;
