}
@media (min-width: 992px) and (max-width: 1199px) {
  .dm-menu {
    left: 0;
  }
}
@media (min-width: 1200px) {
  .dm-menu {
    left: 50%;
    transform: translate(-50%, 10px);
  }
}
.dm-menu.show {
