#tribe-events-pg-template {
	max-width: 100%;
	padding: 0;
}
.type-tribe_events {
	padding-bottom: var(--spacing-6);
}
.tribe-events-content {
	h2 {
		font-size: var(--font-size-h5);
		border-bottom: solid 1px var(--color-blue-4);
		padding-bottom: 0.5em;
	}
}
.events-footer {
	--tec-spacer-7: 0;
	display: flex;
	flex-wrap: wrap;
	gap: var(--gap);
	align-items: flex-start;
}
.tribe-events-virtual-single-marker {
	--tec-color-icon-active: var(--color-red);
	margin: 0;
	padding: 0;
	font-family: var(--font-family-semi-bold);
}
// .tribe-events-virtual-link-button {
// 	@include utils.btnBase;
// 	.tribe-common-c-svgicon--play {
// 		display: none;
// 	}
// }
.related-events {
	border-top: solid 1px var(--color-blue-4);
	border-bottom: solid 1px var(--color-blue-4);
	padding-block: var(--spacing-6);
	.grid {
		display: grid;
		gap: var(--gap);
		@media (min-width: #{utils.getBreakpoint('lg')}){
			grid-template-columns: repeat(3, 1fr);
		}
	}
	.tribe-events-virtual-single-marker{
		float: none;
		justify-content: flex-start;
		font-size: var(--small-text);
		color: var(--color-text);
		display: flex; // default is display: none on mobile for some reason
	}
}

.event-card {
	text-decoration: none;
	display: grid;
	grid-template-columns: #{utils.makeClamp(60, 90)} auto;
	gap: var(--gap);
	align-items: center;
	&:hover {
		text-decoration: none;
		h3 {
			color: var(--color-red);
		}
	}
	.meta {
		color: var(--color-text);
		font-size: var(--small-text);
	}
	h3 {
		margin: 0;
	}
}
