@use "../utils";

.slider-pagination {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	gap: 10px;
	margin-bottom: #{utils.makeClamp(10, 20)};
}
.swiper-pagination-bullet {
	position: relative;

	&:before {
		background: var(--color-blue-3);
		content: "";
		display: block;
		height: 3px;
		left: 0;
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}
.swiper-pagination-bullet-active {
	&:before {
		background: var(--color-white);
	}
}
.slider-controls {
	align-items: center;
	display: inline-flex;
	font-size: #{utils.makeClamp(13, 14)};
	gap: 20px;
	justify-content: space-between;

	@media (max-width: #{utils.getBreakpoint('md', true)}) {
		width: 100%;
	}
}
.slider-paging-label {
	font-size: #{utils.makeClamp(16, 18)};
}
.slider-paging {
	display: flex;
}
.slider-btn {
	align-items: center;
	appearance: none;
	background: transparent;
	border-radius: 0;
	border: solid 3px var(--color-blue-1);
	color: var(--color-blue-1);
	display: flex;
	width: #{utils.makeClamp(40, 60)};
	aspect-ratio: 1;
	justify-content: center;
	text-decoration: none;
	transition: all var(--transitions);
	user-select: none;
	@media (hover: hover) {
		&:hover {
			background: var(--color-buttons-bg-hover);
		}
	}

	&:not(:disabled) {
		cursor: pointer;
	}

	&:last-child {
		margin-left: -3px;
	}
}
.swiper-button-disabled {
	opacity: 0.5;
}

//
.slider-feature {
	.slider-arrows {
		display: flex;
		position: absolute;
		pointer-events: none;
		z-index: 1;
		right: 2rem;
		top: 0;

		@media (min-width: #{utils.getBreakpoint('md')}) {
			justify-content: space-between;
			left: 0;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	.slider-prev,
	.slider-next {
		border-color: var(--color-white);
		color: var(--color-white);
		pointer-events: all;
		@media (hover: hover) {
			&:hover {
				background: rgba(255,255,255,.1);
			}
		}
	}
	.swiper-slide-active {
	}
}

//
.slider-articles {
	overflow: hidden;

	.swiper-slide-active {
		width: 100%;
	}
}

//
.slider-single {
	overflow: hidden;

	.swiper-wrapper {
		transition: transform 1.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
	}
	.swiper-slide-active {
		width: 100%;
	}
}

.resizable::after,
.resizable::before {
	user-select: none;
}
