@use "../utils";
.site-footer {
	font-size: var(--small-text);
	display: grid;
	gap: var(--gap);
	padding-block: var(--spacing-4);
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		grid-template-columns: 23.4% 1fr;
	}
	> div:last-child {
		display: grid;
	}
	.site-contact-details {
		display: grid;
		gap: 20px;
		margin-top: 30px;
		@media (max-width: #{utils.getBreakpoint('lg', true)}) {
			display: none;
		}
	}
}
#footer-menus {
	display: grid;
	gap: 2rem;
	grid-template-columns: repeat(5, minmax(0, 1fr));
	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		display: none;
	}
	ul {
		@include utils.resetList;
		display: grid;
		gap: 5px;
		margin-top: #{utils.makeClamp(8, 10)};

		a {
			text-decoration: none;
			@media (hover: hover) {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}
.footer-menu > strong {
	color: var(--color-red);
}

@media (max-width: #{utils.getBreakpoint('lg', true)}) {
	.footer-logo {
		margin: 0 auto;
	}
}

.copyright {
	font-size: var(--small-text);
	color: var(--color-gray);
	margin-top: auto;

	@media (max-width: #{utils.getBreakpoint('lg', true)}) {
		text-align: center;
	}
}
