@use "../utils";
.post-type-archive-staff {
	.article-header {
		border: 0;
		padding-bottom: var(--spacing-4);
	}
	.team-heading {
		text-align: center;
		color: var(--color-red);
		font-size: var(--font-size-h3);
		margin-bottom: var(--spacing-3);
	}
	.grid {
		@include utils.resetList;
		border-top: solid 1px var(--color-blue-4);
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		text-align: center;
		font-size: var(--small-text);
		gap: #{utils.makeClamp(50, 100)};
		padding-block: var(--spacing-4) var(--spacing-6);
	}
	.photo {
		max-width: 200px;
		margin-inline: auto;
		margin-top: 0;
		& + h3 {
			margin-top: 0;
		}
	}
	.list-dividers {
		margin-top: var(--spacing-2);
		a {
			color: var(--color-anchors);
		}
	}
}
.single-staff {
	.photo {
		margin-bottom: var(--spacing-3);
	}
	.list-dividers {
		a {
			color: var(--color-anchors);
		}
	}
	.grid {
		padding-block: var(--spacing-6);
		display: grid;
		gap: var(--gap);
		@media (min-width: #{utils.getBreakpoint('md')}) {
			grid-template-columns: repeat(12, 1fr);
			> div:first-child {
				grid-column: 1 / span 4;
			}
			> div:last-child {
				grid-column: 6 / span 7;
			}
		}
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			> div:first-child {
				grid-column: 2 / span 4;
			}
			> div:last-child {
				grid-column: 7 / span 5;
			}
		}
	}
}
