@use "../utils";
.paging {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding-top: var(--spacing-5);
	font-size: var(--small-text);
	.dropdown {
		@media (min-width: #{utils.getBreakpoint('md')}) {
			order: 3;
		}
	}
	.dm-small {
		left: auto;
		right: 0;
		transform: translateX(0);

		.active {
			color: var(--color-red);
			font-family: var(--font-family-semi-bold);
		}
	}
}
.paging-num {
	color: var(--color-blue-2);

	@media (min-width: #{utils.getBreakpoint('md')}) {
		order: 1;
	}
}
.pagination {
	@media (max-width: #{utils.getBreakpoint('md', true)}) {
		margin-top: 2rem;
		width: 100%;
	}
	@media (min-width: #{utils.getBreakpoint('md')}) {
		order: 2;
	}
}
.nav-links {
	display: flex;
	gap: 6px;
	justify-content: center;
}
.page-numbers {
	align-items: center;
	color: var(--color-blue-2);
	display: flex;
	font-size: var(--small-text);
	height: #{utils.makeClamp(35, 40)};
	aspect-ratio: 1;
	justify-content: center;
	position: relative;
	text-decoration: none;

	&:after {
		background: var(--color-red);
		bottom: 0;
		content: "";
		display: block;
		height: 2px;
		left: 10px;
		opacity: 0;
		position: absolute;
		right: 10px;
	}
	&.current {
		color: var(--color-red);

		&:after {
			opacity: 1;
		}
	}
	&:hover {
		color: var(--color-red);
	}
	&.prev,
	&.next {
		border: solid 2px;
		color: var(--color-blue-1);

		&.is-disabled {
			border-color: var(--color-blue-2);
			color: var(--color-blue-2);
			opacity: 0.5;
			pointer-events: none;
		}
	}
	// &.prev {
	// 	padding-right: #{utils.makeClamp(5, 10)};
	// }
	// &.next {
	// 	padding-left: #{utils.makeClamp(5, 10)};
	// }
}
