@use "../utils";
.dev-message {
	margin-block: var(--spacing-2);
	padding: var(--spacing-2);
	text-align: center;
	background: lightcyan;
}
* {
	box-sizing: border-box;
}

*:focus-visible {
	@include utils.focusStyle;
}

html {
	scroll-behavior: smooth;
}

body {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	font-family: var(--font-family-base);
	font-size: var(--font-size-base);
	line-height: var(--line-height-base);
	color: var(--color-text);

	&.no-scroll {
		overflow: hidden;
	}
}

body > a[href="#siteMain"] {
	position: absolute;
	z-index: 1000;
}

.screen-reader-text,
.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
	@include utils.visually-hidden;
}

a {
	color: var(--color-anchors);
	// text-decoration: none;
	&:hover {
		text-decoration: none;
	}
}

// this isn't included in buttons.scss to protect admin buttons
button,
[type="submit"] {
	@include utils.resetButton;
}

figure {
	margin-inline: 0;
}
video,
svg,
img {
	display: block;
	max-width: 100%;
	height: auto;
}
picture {
	display: block;
}
b,
strong {
	font-weight: 400;
	font-family: var(--font-family-semi-bold);
}

p,
ol,
ul,
dl,
table,
figure,
blockquote {
	margin-block: 0.4em 1.25em;
}

hr{
	border: 0;
	border-top: solid 1px var(--color-borders);
}

.wp-block-image {
	&.is-style-badge {
		position: relative;
		img {
			width: 100%;
		}
		.wp-element-caption {
			position: absolute;
			right: 0;
			top: 0;
			background: var(--color-blue-2);
			color: var(--color-white);
			font-size: var(--small-text);
			padding: 0.25em 0.5em;
		}
	}
}

.wp-block-separator {
	margin: 1.25em 0;
	width: 53px;
	border: 0;
	height: 3px;
	background: var(--color-red);

	&.is-style-wide {
		height: 1px;
		width: 100%;
		background: var(--color-blue-4);
	}
}

.page-content {
	// constrain width of all elements
	> * {
		max-width: var(--content-max-width);
		margin-inline: auto;
	}
	// full width for custom blocks and groups
	> .wp-block-group,
	[class*="wp-block-bespoke"] {
		max-width: 100%;
	}
	// when we tell something to be container width
	.is-style-wide {
		max-width: var(--wide-max-width);
		margin-inline: auto;
	}
}

.wp-block-group {
	padding-block: var(--spacing-7);

	> .wp-block-heading {
		max-width: var(--wide-max-width);
	}

	> .wp-block-pullquote{
		@media (max-width: #{utils.getBreakpoint('lg', true)}){
			padding-inline: 2em;
		}
	}

	// groups apply dumb margin rules automatically
	[class*="wp-block-bespoke"],
	[class*="wp-block-bespoke"]:first-child,
	[class*="wp-block-bespoke"]:last-child{
		margin: 0;
	}
}

.site-contact-details {
	address {
		font-style: normal;
	}
	strong {
		color: var(--color-blue-1);
		font-weight: 400;
	}
	a {
		color: var(--color-black);
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	> * {
		margin: 0;
	}
}

@keyframes animateDropdownUp {
	from {
		opacity: 0;
		visibility: hidden;

		@media (min-width: #{utils.getBreakpoint('lg')}) {
			transform: scale(0.95);
			transform-origin: center center;
		}
	}
	to {
		opacity: 1;
		visibility: visible;

		@media (min-width: #{utils.getBreakpoint('lg')}) {
			transform: scale(1);
			transform-origin: center center;
		}
	}
}
@keyframes animateDropdownMobile {
	from {
		opacity: 0;
		visibility: hidden;
	}
	to {
		opacity: 1;
		visibility: visible;
	}
}

.not-found {
	margin-block: var(--spacing-6);
	padding: #{utils.makeClamp(40, 100)};
	background: var(--color-blue-5);
	text-align: center;
	font-family: var(--font-family-headings);
	color: var(--color-blue-1);
	font-size: var(--font-size-h5);
	p {
		margin: 0;
	}
}
