@use "../utils";
.card-article {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: space-between;
	text-decoration: none;
	@media (min-width: #{utils.getBreakpoint('lg')}){
		gap: var(--spacing-4);
	}

	@media (hover: hover) and (pointer: fine) {
		&:hover {
			color: inherit;
			text-decoration: none;
			[class^="heading-"] {
				color: var(--color-red);
			}
			figure {
				img {
					transform: scale(1.05);
				}
			}
		}
	}

	figure {
		margin: 0;
		margin-bottom: #{utils.makeClamp(20, 23)};
		overflow: hidden;
		position: relative;
		width: 100%;
		img {
			transition: var(--transitions);
		}
	}
	.text-small {
		color: var(--color-blue-1);
		margin-bottom: 0;
	}
	[class^="heading-"] {
		color: var(--color-blue-1);
	}
	.card-body {
		p {
			margin: 0;
		}
	}
	.card-footer {
		align-items: center;
		color: var(--color-blue-2);
		display: flex;
		gap: #{utils.makeClamp(10, 15)};
		margin-top: var(--spacing-2);
	}
}
