@use "../utils";
#siteMain {
	--tec-grid-gutter-half: 0;
	--tec-grid-gutter-half-negative: 0;
	--tec-grid-width: var(--wide-max-width);
	--tec-color-text-events-title: var(--color-blue-1);
	--tec-color-border-secondary-month-grid: var(--color-blue-4);

	.tribe-common-g-row {
		display: block;
	}
	.tribe-common-g-row--gutters{
		margin-inline: 0;
	}
	.tribe-events-l-container {
		padding: 0;
		justify-content: space-between;
		gap: 0 var(--gap);
	}
	.tribe-events-c-subscribe-dropdown__button {
		@include utils.btnBase;
		.tribe-common-c-svgicon--cal-export {
			display: none;
		}
		.tribe-events-c-subscribe-dropdown__button-text {
			font-size: var(--font-size-buttons);
			color: currentColor;
			line-height: 1;
		}
		// tribe managed to screw up the rotated state
		.tribe-events-c-subscribe-dropdown__button-icon--rotate {
			transform: none;
		}
		.tribe-events-c-subscribe-dropdown__button-text {
			&[aria-expanded="true"]
				+ .tribe-events-c-subscribe-dropdown__button-icon {
				transform: scaleY(-1);
			}
		}
		.tribe-events-c-subscribe-dropdown__button-icon {
			transition: transform var(--transitions);
			transform: scaleY(1);
		}
	}
	.tribe-events-c-subscribe-dropdown__list {
		@include utils.resetList;
		gap: 0.6em;
		transform: none;
		li::before {
			display: none;
		}
		a {
			@include utils.dropdownLink;
		}
	}
	.tribe-events-c-subscribe-dropdown__content {
		--tec-spacer-0: 0;
		@include utils.dropdownBase;
		background: var(--color-white);
		z-index: 100;
	}

	// breaking these up to make this more organized
	@import "events/utilities";
	@import "events/filters";
	@import "events/list";
	@import "events/calendar";
}

.tribe-events-hybrid-single-marker,
.tribe-events-virtual-single-marker{
	float: none;
}
.tribe-events-virtual-virtual-event {
	font-family: var(--font-family-semi-bold);
	.tribe-events-virtual-virtual-event__icon {
		color: var(--color-red);
	}
}

#siteMain .tribe-events-calendar-list__event-date-tag-datetime,
.event-card .date {
	transition: background-color var(--transitions);
	background: var(--color-blue-2);
	color: var(--color-white);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-block: 20px;
	line-height: 0.9;
}
.tribe-events-calendar-list__event-date-tag-datetime .tribe-events-calendar-list__event-date-tag-weekday,
.event-card.day-name {
	text-transform: uppercase;
	font-family: var(--font-family-semi-bold);
}
.tribe-events-calendar-list__event-date-tag-datetime .tribe-events-calendar-list__event-date-tag-daynum,
.event-card .day-num {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-h1);
}

.tooltipster-base.tribe-events-tooltip-theme .tooltipster-box{
	background: var(--color-blue-5);
    box-shadow: 0 0 10px rgba(0,0,0,.2);
	border: solid 1px var(--color-blue-4);
	padding: 1em;
}

.tooltipster-sidetip .tooltipster-content{
	color: var(--color-text);
    font-size: var(--small-text);
    line-height: 1.3;
} 
.tribe-events-calendar-month__calendar-event-tooltip-title{
	font-size: var(--font-size-h6);
	a{
		color: currentColor;
	}
}


@import "events/archive";
@import "events/single";
