@use "../utils";
.single:not(.single-resource) {
	main {
		--color-anchors: var(--color-blue-2);
	}
	main > article {
		padding-bottom: var(--spacing-6);
		+ section {
			padding-bottom: var(--spacing-6);
		}
	}
	.article-header {
		border: 0;
		.meta {
			margin-top: var(--spacing-2);
			font-size: var(--small-text);
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 1em;
			.posted-by + .posted-on {
				margin-left: 0.75em;
				padding-left: 0.75em;
				border-left: solid 1px var(--color-blue-4);
			}
		}
		.social {
			--color-anchors: var(--color-black);
		}
	}
	.featured-image {
		margin-bottom: #{utils.makeClamp(30, 40)};
	}
}

.article-content {
	--quote-width: 48.4%;
	--margin-around: 2em;
	max-width: min(var(--vw-max-width), 930px);
	margin-inline: auto;

	> * {
		max-width: var(--content-max-width);
		margin-inline: auto;
	}

	> .wp-block-pullquote {
		margin-block: var(--margin-around);
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			&.alignleft {
				max-width: var(--quote-width);
				margin-right: var(--margin-around);
				float: left;
			}
			&.alignright {
				max-width: var(--quote-width);
				float: right;
				margin-left: var(--margin-around);
			}
		}
		blockquote {
			@include utils.accent;
			margin: 0;
		}
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		> .wp-block-image {
			&.alignwide {
				max-width: 100%;
				img {
					width: 100%;
				}
			}
			&.alignleft {
				margin-top: 0;
				margin-right: var(--margin-around);
				float: left;
			}
			&.alignright {
				margin-top: 0;
				float: right;
				margin-left: var(--margin-around);
				display: block; // we have a global rule that does a display flex;
			}
			&.aligncenter {
				flex-wrap: wrap;
				.wp-element-caption {
					text-align: center;
				}
			}
		}
	}

	.wp-element-caption {
		font-size: var(--small-text);
		margin-top: 1em;
		width: 100%;
	}

	h2,
	h3,
	h4,
	h5,
	h6 {
		text-wrap: initial;
	}

	ul,
	ol {
		display: grid;
		gap: #{utils.makeClamp(10, 20)};
		list-style-position: inside;
		padding: 0;
	}
	ul {
		list-style: none;

		li {
			padding-left: 1em;
			position: relative;

			&:before {
				background: var(--color-blue-2);
				border-radius: 50%;
				content: "";
				display: block;
				height: 0.56em;
				left: 0;
				position: absolute;
				top: 0.44em;
				width: 0.56em;
			}
		}
	}
}

.article-footer {
	max-width: var(--content-max-width);
	margin-inline: auto;
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	margin-top: var(--spacing-3);
	justify-content: center;
	a {
		color: var(--color-anchors);
	}
}

.single-career {
	.grid {
		max-width: var(--wide-max-width);
		margin-inline: auto;
		display: grid;
		gap: 0 var(--gutter);
		grid-template-columns: 1fr 690px 1fr;
		align-items: start;
	}
	.article-content {
		grid-column: 2 / 3;
		> *:first-child {
			margin-top: 0;
		}
	}
}
#apply-cta {
	border-left: solid 1px var(--color-blue-4);
	padding-left: var(--gutter);
	padding-block: 1em;
	position: sticky;
	top: var(--gutter);
	h2 {
		font-size: var(--font-size-h6);
		font-family: var(--font-family-semi-bold);
		margin: 0;
	}
	p {
		margin-block: 0.5em;
	}
}