.tribe-events-c-view-selector__content{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		background: var(--color-white);
		box-shadow: 0 0 5px rgba(0,0,0,.2);
		padding: var(--spacing-2);
		width: 50vw;
	}
}

.tribe-events-c-events-bar__search-container{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		background: var(--color-white);
		box-shadow: 0 0 5px rgba(0,0,0,.2);
		padding: var(--spacing-2);
		top: 100%;
		transform: translateY(10px);
	}
	@media (min-width: #{utils.getBreakpoint('lg')}){
		margin-top: var(--spacing-3);
		border-bottom: solid 1px var(--color-blue-4);
	}
}
.tribe-events-c-events-bar__search-form{
	flex-direction: row-reverse;
	justify-content: center;
	width: auto;
	gap: 20px;
	display: flex;
}
.tribe-events-c-events-bar__search{
	justify-content: center;
}
.tribe-events-c-search__input-group{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		flex: 1;
	}
}
.tribe-events-c-search__input-control{
	margin: 0;
}
.tribe-events-c-search__input{
	margin: 0;
	padding: var(--field-padding);
	width: auto;
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		width: 100%;
		padding: 0;
		min-height: 1px;
	}
	&::placeholder{
		color: var(--color-blue-2);
	}
	
}
.tribe-events-c-search__button{
	padding: 0;
	color: var(--color-blue-1);
	margin: 0;
	width: auto;
}

.tribe-events-header{
	padding: 0;
	margin: var(--spacing-3) 0;
	@media (min-width: #{utils.getBreakpoint('lg')}){
		margin: 0;
	}
}
.tribe-events-c-messages__message--notice{
	gap: 1em;
	color: var(--color-red);
	background: #F5F8FB;
	line-height: 1;
}

.tribe-events-header__top-bar{
	justify-content: space-between;
	gap: var(--gap);
}

.tribe-events-header__top-bar .main,
.tribe-events-calendar-month,
.tribe-events-calendar-list {
	width: auto;
	max-width: 930px;
	flex: 1;
}

.tribe-filter-bar__form-heading,
.tribe-events-calendar-list__month-separator {
	font-size: var(--font-size-h5);
	padding: 0;
	margin-bottom: 0.9em;
}
.tribe-events-c-top-bar__datepicker-container .dropdown-menu{
	top: 0 !important;
}
.tribe-events-c-top-bar__datepicker{
	flex: 1;
	margin-left: auto;
}
.tribe-events-c-top-bar__datepicker-button{
	font-size: var(--font-size-h1);
	font-family: var(--font-family-headings);
	color: var(--color-blue-1);
	gap: .5em;
}
.tribe-events-c-top-bar__datepicker-button-icon-svg{
	display: block;
	width: 16px;
	height: 10px;
	@media (min-width: #{utils.getBreakpoint('lg')}){
		width: 21px;
		height: 13px;
		
	}
	.tribe-common-c-svgicon__svg-fill{
		fill: currentColor;
	}
}

.tribe-events .datepicker table{
	width: 100%;
}


.tribe-events-header__top-bar .main{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.tribe-events-header__top-bar .sidebar{
	display: flex;
	gap: var(--gap);
	align-items: center;
	width: #{utils.makeClamp(180, 330)};
}
.tribe-events-c-top-bar__today-button{
	@include utils.btnBase;
	@include utils.btnSecondary;
	margin: 0;
}
.tribe-events-c-top-bar__nav-list{
	gap: 20px;
}
.tribe-events-c-top-bar__nav-list-item{
	padding: 0;
	a{
		color: var(--color-blue-1);
	}
}

.tribe-events-c-view-selector__list{
	gap: var(--gap);
	display: flex;
	.tribe-events-c-view-selector__list-item{
		padding: 0;
		flex: 1;
		justify-content: center;
		gap: 6px;
		display: flex;
		&:not(.tribe-events-c-view-selector__list-item--active){
			a{
				color: var(--color-text);
				border-top-color: transparent;
			}	
		}
		a{
			@include utils.btnBase;
			@include utils.btnSecondary;
			@media (max-width: #{utils.getBreakpoint('lg', true)}){
				width: 100%;
			}
		}
		
	}
}
.tribe-events-c-view-selector__list-item-icon{
	margin: 0 auto 6px;
	width: 20px;
	height: 20px;
	display: block;
}
.tribe-common-c-svgicon--day,
.tribe-common-c-svgicon--map,
.tribe-common-c-svgicon--month,
.tribe-common-c-svgicon--photo,
.tribe-common-c-svgicon--week{
	height: auto;
}
