.tribe-events-calendar-month{
	@media (min-width: #{utils.getBreakpoint('lg')}){
		padding-bottom: var(--spacing-6);
	}
}
.tribe-events-calendar-month__day-cell--mobile{
	top: 8px;
	gap: 2px;
}
.tribe-events-calendar-month__header-column-title{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		text-align: center;
	}
}
.tribe-events-calendar-month__header-column-title-desktop{
	font-size: var(--font-size-h5);
}
.tribe-events .tribe-events-calendar-month__week{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		gap: 1px;
		margin-bottom: 1px;
	}
}
.tribe-events-calendar-month__day--past{
	color: #737373;
	background: #F5F8FB;
	a{
		color: #737373;
	}
}
.tribe-events-calendar-month-mobile-events__day-marker{
	font-family: var(--font-family-headings);
	color: var(--color-blue-1);
	font-size: var(--font-size-h2);
}
.tribe-events-calendar-month__day-date{
	font-size: var(--font-size-h6);
	font-family: var(--font-family-semi-bold);
	color: currentColor;
}
.tribe-events-calendar-month__calendar-event-details{
	font-size: var(--small-text);
	
}
.tribe-events-calendar-month__calendar-event-datetime{
	font-size: .8em;
}
.tribe-events-calendar-month__calendar-event-title{
	font-size: var(--font-size-h6);
	font-family: var(--font-family-semi-bold);
	font-size: 1em;
}
.tribe-events-calendar-month .tribe-events-virtual-hybrid-event,
.tribe-events-calendar-month .tribe-events-virtual-virtual-event{
	display: block;
}

.tribe-events-calendar-list__event-date-tag{
	width: 100%;
}

