@use "../utils";
.current-episode {
	display: grid;
	gap: var(--gap);
	@media (min-width: #{utils.getBreakpoint('lg')}){
		grid-template-columns: repeat(12, 1fr);
		> div:first-child {
			grid-column: span 8;
		}
		> div:last-child {
			grid-column: 10 / span 3;
		}
	}
	.summary {
		margin-top: var(--spacing-3);
		@media (min-width: #{utils.getBreakpoint('lg')}){
			max-width: 50%;
		}
		.btn-row {
			margin-top: var(--spacing-2);
		}
		.text-small {
			color: var(--color-blue-2);
			margin-top: var(--spacing-2);
		}
	}
	.btn-group {
		display: grid;
		gap: var(--gap);
	}
}
.single-podcast {
	.current-episode {
		@media (min-width: #{utils.getBreakpoint('lg')}){
			padding-block: var(--spacing-5);
		}
		.btn-row {
			display: none;
		}
	}
}
