@use "../utils";
//archive
.community-link{
	display: inline-block;
	color: var(--color-blue-2);
	&:hover{
		color: var(--color-blue-1);
	}
}
.state{
	--fill: var(--color-blue-5);
	fill: var(--fill);
	&:focus{
		outline: none;
	}
}
.abbr{
	--fill: var(--color-blue-3);
	fill: var(--fill);
	pointer-events: none;
}
.community-name{
	text-decoration: none;
	&:hover{
		color: var(--color-blue-1);
	}
	@media (max-width: #{utils.getBreakpoint('sm', true)}){
		text-decoration: underline;
	}
}
//single
.single-community{
	// decorative divider bar thing
	.bg{
		margin-top: var(--spacing-3);
		aspect-ratio: 141 / 12;
	}
}
.community-details{
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	gap: var(--gap);
	align-items: center;
	padding-block: var(--spacing-6);

	address{
		font-style: normal;
	}

	> div:first-child{
		grid-column: 1 / span 6;
	}
	> div:last-child{
		grid-column: 8 / span 4;
	}
}
#communities-map{
	position: relative;	
	max-width: min(var(--vw-max-width), 1266px);
	margin-inline: auto;
	svg{
		width: 100%;
	}
	.tooltip.hi{
		text-align: left;
		padding: 2em;
	}
}
.tooltip-trigger.hi{
	// background: red;
	width: 11.85%;
	height: 2%;
	position: absolute;
	top: 75%;
	left: 0;
}
.tooltip{
	pointer-events: none;
	opacity: 0;
	transition: var(--transitions);
	position: absolute;
	background: var(--color-white);
	padding: 1em;
	font-size: var(--small-text);
	text-align: center;
	max-width: #{utils.makeClamp(200, 395)};
	min-width: 100px;
	transform: translate(-50%, -95%);
	box-shadow: 0px 2.767px 2.214px rgba(0, 0, 0, 0.02),
	0px 2.767px 2.214px rgba(0, 0, 0, 0.02),
	0px 6.65px 5.32px rgba(0, 0, 0, 0.03),
	0px 6.65px 5.32px rgba(0, 0, 0, 0.03),
	0px 12.522px 10.017px rgba(0, 0, 0, 0.04),
	0px 12.522px 10.017px rgba(0, 0, 0, 0.04),
	0px 22.336px 17.869px rgba(0, 0, 0, 0.04),
	0px 22.336px 17.869px rgba(0, 0, 0, 0.04);

	&.shown{
		opacity: 1;
		transform: translate(-50%, -100%);
	}

	&::after{
		content: '';
		position: absolute;
	}

	&.top{
		&::after{
			border-top: solid 10px var(--color-white);
			border-left: solid 10px transparent;
			border-right: solid 10px transparent;
			top: 100%;
			left: 50%;
			transform: translate(-50%, -1px);
		}
	}

	&.right{
		transform: translate(10px, -45%);
		&::after{
			right: 100%;
			top: 50%;
			transform: translate(1px, -50%);
			border-right: solid 10px var(--color-white);
			border-top: solid 10px transparent;
			border-bottom: solid 10px transparent;
		}
		&.shown{
			transform: translate(10px, -50%);
		}
	}

	> *:first-child{
		margin-top: 0;
	}
	> *:last-child{
		margin-bottom: 0;
	}
}
