.tribe-events-calendar-list__month-separator:first-child{
	margin-top: 0;
}
.tribe-events-calendar-list__month-separator:not(:first-child) {
	margin-top: var(--spacing-4);
}

.tribe-events-calendar-list__event-row {
	margin: 0;
	display: grid;
	gap: var(--gap);
	grid-template-columns: #{utils.makeClamp(60, 90)} auto;
	align-items: start;
	padding-block: var(--spacing-4);
	border-top: solid 1px var(--color-blue-4);
	border-bottom: solid 1px var(--color-blue-4);
	+ .tribe-events-calendar-list__event-row {
		border-top: 0;
	}
	&:last-child{
		border-bottom: 0;
	}
}
.tribe-events-calendar-list__event-wrapper{
	width: auto;
}
.tribe-events-calendar-list__event-details {
	width: auto;
	padding: 0;
}
.tribe-events-calendar-list__event-title{
	a:hover{
		text-decoration: none;
		color: var(--color-red);
	}
}

.tribe-events-calendar-list__event-venue,
.tribe-events-calendar-list__event-datetime-wrapper{
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		font-size: var(--small-text);
	}
}
